import React, { Component } from "react";
import axios from "axios";

import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import swal from "sweetalert";

import "../../styles/components/login.css";

export class SignIn extends Component {
  constructor(props) {
    super(props);

    this.onChangeCorreoUsuario = this.onChangeCorreoUsuario.bind(this);
    this.onChangeContraseaUsuario = this.onChangeContraseaUsuario.bind(this);
    this.onSubmit = this.onSubmit.bind(this);

    this.state = {
      correo: "",
      contrasea: "",
    };
  }

  onChangeCorreoUsuario(e) {
    this.setState({ correo: e.target.value });
  }
  onChangeContraseaUsuario(e) {
    this.setState({ contrasea: e.target.value });
  }
  onSubmit(e) {
    e.preventDefault();

    const UsuarioObject = {
      correo: this.state.correo,
      contrasea: this.state.contrasea,
    };

    const validacion = (correo, contrasea, data) => {
      let auth = false;
      data.forEach((element) => {
        if (correo === element.correo) {
          if (contrasea === element.contrasea) {
            auth = true;
          }
        }
      });
      return auth;
    };

    axios.get("http://localhost:4000/usuarios").then((res) => {
      if (
        validacion(UsuarioObject.correo, UsuarioObject.contrasea, res.data)
      ) {
        swal({
          title: "Correcto!",
          text: "Validacin exitosa",
          icon: "success",
          timer: 2350,
          buttons: false,
        }).then(() => {
          localStorage.setItem("authenticated", "true");
          window.location = "/home";
        });
      } else {
        swal({
          title: "Error",
          text: "Usuario o contrasea invlidos",
          icon: "error",
          button: "Listo",
        });
      }
    });
  }

  render() {
    return (
      <div id="login-container">
        <div className="form-signin text-center w-100 m-auto pt-0">
          <Form onSubmit={this.onSubmit}>
            <img
              className="mb-3 logo"
              src="https://cdn1.iconfinder.com/data/icons/zeon-medical-i-2/32/pharmacy_shop_drug_medicine-256.png"
              alt="logo"
            />

            <h1 className="h3 mb-3 fw-normal">Inicio de sesin</h1>

            <Form.Group className="form-floating"></Form.Group>

            <Form.Group className="form-floating">
              <Form.Control
                type="email"
                id="floatingUser"
                placeholder=" "
                className="form-control"
                value={this.state.correo}
                onChange={this.onChangeCorreoUsuario}
                required
              />
              <Form.Label>Correo</Form.Label>
            </Form.Group>
            <Form.Group className="form-floating">
              <Form.Control
                type="password"
                id="floatingPassword"
                placeholder=" "
                className="form-control"
                value={this.state.contrasea}
                onChange={this.onChangeContraseaUsuario}
                required
              />
              <Form.Label>Contrasea</Form.Label>
            </Form.Group>

            <p>
              Eres nuevo por aqu? <a href="/registrarse">Regstrate</a>
            </p>

            <div className="d-grid">
              <Button type="submit" className="w-100 btn btn-lg btn-success ">
                Entrar
              </Button>
            </div>
          </Form>
        </div>
      </div>
    );
  }
}
